@import url('https://fonts.googleapis.com/css2?family=Almarai:wght@300;400;700;800&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

html{
  background: linear-gradient(to bottom, rgb(15, 23, 42), transparent),
  url(https://grainy-gradients.vercel.app/noise.svg);
  font-family: 'Poppins', sans-serif;
}

.App {
  text-align: center;
  min-height: 100vh;
  color:white;

}

.header{
  padding: 1.5rem;
  margin-bottom: 2rem;
}

.title{
  font-size: 1.5rem;
  line-height: 2rem;
  font-weight: 700;
  font-weight: 700;
  text-transform: uppercase;
    
}

.actletter{
  font-family: 'Almarai', sans-serif;
    font-size: 7rem;
    line-height: 1;
    font-weight: 700;
    margin-bottom: 1rem
}

.contcont{
  margin-top: 1rem;
  margin-bottom: 1rem;
  
}

.cont{
  margin-top: 150px;
  display: flex;
  flex: 1 1;
  flex-wrap: wrap;
  margin-left: 20vw;
  margin-right: 20vw;
  gap: 20px;
  padding-bottom: 100px;
}

.btn{
  font-family: 'Poppins','Almarai', sans-serif;
  display: block;
  width: 12rem;
  height: 6rem;
  background-color: rgb(15, 23, 42);
  margin-left: auto;
  margin-right: auto;
  border: none;
  font-size: 3rem;
  line-height: 1;
  text-align: center;
  padding-bottom: 0.5rem;
  border-radius: 0.75rem;
  color:white;
  padding: 18px 25px 18px 25px;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
}
.btn:focus{
  outline:none;
  box-shadow: none;
}
.btn:hover{
  background-color: rgb(70, 73, 116);
  transition: all 0.3s ease-in-out;
}


.error{
  background-color: red;
  margin-top: 8rem;
  font-size: 1.875rem;
  line-height: 2rem;
  font-family: 'Poppins','Almarai', sans-serif;
    position: fixed;
    bottom: 0px;
    width: 100vw;
    height:10vh ;
}

@media only screen and (max-width: 768px) {
 
  .btn{
    width: 7rem;
  height: 4rem;
  font-size: 1.25rem;
 
  }
  .error{
    
    font-size: 1rem;
    padding-top: 5px;
    padding-bottom: 5px;
    line-height: 0rem;
    height: 30px;
  }
  .actletter{
    font-family: 'Almarai', sans-serif;
    font-size: 5rem;
    line-height: 1;
    font-weight: 500;
    margin-bottom: 0rem
  }
}



